body {
  background-image: url("./images/WorldMapBackground.jpg");
  background-size: cover;
  background-attachment: fixed;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.image {
  width: 250px;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}
